                      hide-details="auto"
                      class="mx-2 text-h5"
                      :maxlength="patchNameLength"
                      :counter="patchNameLength"
                      :rules="patchNameRules"
                      :disabled="enableControl"
                  />
                </v-form>
                <v-list-item-subtitle>
                  <span class="patch-no-area">Patch#: <span class="mx-2 patch-num-design">{{ patchname_param.num }}</span></span>
                </v-list-item-subtitle>
              </v-list-item-content>

            </v-list-item>
          </div>
