<script>
 import { sysex_cfg } from '../vanilla/scripts/ewi4000sconst.js';

 export default {
   name: 'PatchNumNameCard',
   props: ['patchnameparam', 'label'],
   data: () => ({
     sync_menus: [
       {
         title: 'Sync to Device',
         iconText: 'mdi-camera-flip',
         color: 'primary',
         action: 'syncToDevice',
         disabled: false
       },
       {
         title: 'Discard Change',
         iconText: 'mdi-file-undo',
         color: 'red accent-2',
         action: 'discardChange',
         disabled: false
       },
     ],
     patchname_param: {
       name: '',
       num: '',
       isSynced: true
     },
     patchNameRules: [
       v => !!v || 'Patch name is required',
       v => v.length <= sysex_cfg.EWI_PATCHNAME_LENGTH || `Name must be less than ${sysex_cfg.EWI_PATCHNAME_LENGTH} characters.`,
       v => v.match(/^[\x20-\x7e]*$/)!=null || `ASCII characters Only.`
     ],
     patchNameLength: sysex_cfg.EWI_PATCHNAME_LENGTH
   }),
   computed: {
     /*
     patchname_param_isSynced: {
       get: function() {
         if (this.$store.getters.getWorkingPatchIdx()==null) {
           return true
         }
         const workingPatch_idx = this.$store.getters.getWorkingPatchIdx()
         return this.$store.getters.getCheckOneEditPatchIsSynced(workingPatch_idx)
       },
       set: function(state) {
         this.$set(this.$data.patchname_param, 'isSynced', state)
       }
     },
      */
     isValidationOK: {
       get: function() {
         return this.$store.getters.getIsValidationOK()
       },
       set: function(newValue) {
         this.$store.commit('onUpdateIsValidationOK', {value: newValue})
       }
     },
     enableControl: {
       get: function() {
         // cloase all panels
         // [TODO] made this line to comment due to avoid closing panels even in the case of discarding parameter changes.
         // vvvv vvvv vvvv
         //this.$store.commit('onUpdateStatusExpandPanel', {value: []})
         if (typeof this.$refs.PatchNumNameCard != 'undefined') {
           this.$refs.PatchNumNameCard.clearPatchNamePatchNo()
         }
         return !this.$store.getters.getEnableControl()
       }
     },
     patchname_param_name: {
       get: function() {
         return this.$data.patchname_param.name.replace(/ +$/, '')
       },
       set: function(newValue) {
         if (newValue.length > sysex_cfg.EWI_PATCHNAME_LENGTH) {
           newValue = newValue.substr(0, sysex_cfg.EWI_PATCHNAME_LENGTH)
         }
         console.log(newValue, newValue.length)
         this.$set(this.$data.patchname_param, 'name', `${newValue}`)
         this.onUpdatePatchName('patchName', newValue)
       }
     }
   },
   created: function() {
     this.patchname_param.name = this.patchnameparam.name
     this.patchname_param.num = this.patchnameparam.num
     this.patchname_param.isSynced = this.patchnameparam.isSynced
     console.log(`<CREATE> label=[${this.label}]`, this.patchname_param)
   },
   watch: {
   },
   methods: {
     dump_patchname_param: function() {
       const workingPatch_idx = this.$store.getters.getWorkingPatchIdx()
       const workingPatch = this.$store.getters.getOneEditPatch(workingPatch_idx)
       console.log(`[store:${this.label}]`, workingPatch.decoded)
       console.log(`[local:${this.label}]`, this.$data.patchname_param.name)
     },
     clearPatchNamePatchNo: function() {
       this.$set(this.$data.patchname_param, 'name', '')
       this.$set(this.$data.patchname_param, 'num', '')
     },
     /*
     openDeviceCommunication: function() {
       this.$router.app.$children[0].openDeviceCommunicationApp(true)
       //this.$refs.router.$refs.MidiDeviceControls.handleDeviceCommunicationDialog(true)
     },
     updateParamIsSynced: function() {
       const workingPatch_idx = this.$store.getters.getWorkingPatchIdx()
       const isSynced = this.$store.getters.getCheckOneEditPatchIsSynced(workingPatch_idx)
       console.log(`[PatchNumNameCard.vue: updateParamIsSynced] isSynced=[${isSynced}]`)
       this.$set(this.$data.patchname_param, 'isSynced', isSynced)
     },
     */
     onUpdatePatchName: function(key, val) {
       //console.log(this.formant_param, key, val, this.$data)
       //const workingPatch_idx = this.$store.getters.getWorkingPatchIdx()
       //const workingPatch = this.$store.getters.getOneEditPatch(workingPatch_idx)
       //console.log(this.label, workingPatch)
       if (val.length < sysex_cfg.EWI_PATCHNAME_LENGTH) {
         const rest_num = Number(sysex_cfg.EWI_PATCHNAME_LENGTH) - val.length
         const rest_letter = ((new Array(rest_num)).fill(' ')).join('')
         val = `${val}${rest_letter}`
       } else {
         val = val.substr(0, sysex_cfg.EWI_PATCHNAME_LENGTH)
       }

       this.$set(this.$data.patchname_param, key, val)
       this.$store.commit('onEditOneEditPatch', {arr_idx: [`${key}Decoded`], value: val})

       const workingPatch_idx = this.$store.getters.getWorkingPatchIdx()
       const workingPatch = this.$store.getters.getOneEditPatch(workingPatch_idx)
       console.log(workingPatch.decoded.isSynced)
       this.$data.patchname_param.isSynced = workingPatch.decoded.isSynced
       this.$router.app.$children[0].updateParamIsSyncedApp()
     },
     //
     //
     /*
     onIsSyncedMenuClicked(action=null) {
       if (action==null) return
       switch(action) {
         case 'syncToDevice':
           console.log(this.$router)
           this._onSyncParamEditOnePatchToDevice()
           break
         case'discardChange':
           this._onDiscardChange()
           break
       }
     },
     _onSyncParamEditOnePatchToDevice() {
     this.$router.app.$children[0].syncParamEditOnePatchToDeviceApp()
     },
     _onDiscardChange() {
       this.$router.app.$children[0].discardParamEditOnePatchApp()
     }
     */
   },
 }
</script>
