       loading: false,
       closeBtnDisabled: false
     },
     connection_method_tab: 'wired-midi',
     ble_button: {
       color: {
         btn: '', // primary
         text: ''
       },
       text: 'Connect',
       loading: false,
       icon: 'mdi-bluetooth'
     },
     /*
     icons: {
       mdiMidiPort: mdiMidiPort,
       mdiDownload: mdiDownload,
       mdiTune: mdiTune,
       mdiRefresh: mdiRefresh,
       mdiBluetooth: mdiBluetooth,
       mdiCableData: mdiCableData,
       mdiBluetoothConnect: mdiBluetoothConnect,
       mdiReload: mdiReload,
     },
     */
     midi_on_state_change: {
       color_set: {
         normal: '#212121',
         changed: 'deep-orange',
       },
       color: '#212121',
       timerId: null
     },
     select_midi_device_disabled: false,
     select_midi_device_btn_disabled: true,
     is_checking_midi_device: false,
     midi_device_dialog: false,
     component_overly_w_dialog: {
       dialog_title: 'Fetching Patches',
       text: '',
       btnText: 'Stop',
       btnColor: 'red',
       btnDisabled: true,
       progressValue: 0,
       progressValueNum: 0,
       progressStream: true,
       progressColor: 'indigo darken-1',
       show: false,
       closeDialogShowDuration: 2200,
       timerId: 0,
     },
