       output: -1
     },
   }),
   computed: {
     demoMode: {
       get: function() {
         const demoMode = this.$store.getters.getDemoMode()
         this.$set(this.$data, 'select_midi_device_disabled', demoMode)

         // update param of disabled of "USE THESE PORTS" button
         // handle "midi_device_alert_message"
         if (demoMode) {
           const  tmp_midi_device_alert_message = {
             text: '',
             type: 'info',
             show: false
           }
           this.$set(this.$data, 'midi_device_alert_message', tmp_midi_device_alert_message)
           this.$set(this.$data, 'select_midi_device_btn_disabled', true)
         } else
         if (this.sel_midi_device_output==null || this.sel_midi_device_input==null) {
           this.$set(this.$data, 'select_midi_device_btn_disabled', true)
         } else {
           this.$set(this.$data, 'select_midi_device_btn_disabled', false)
         }

         return demoMode
       },
       set: function(value) {
         this.$store.commit('onUpdateDemoMode', {value: value})
         this.$router.app.$children[0].switchDemoDevicePatchesApp(value)
       }
     },
     midiDeviceSelected: {
       get: function() {
         const midi_devices = this.$store.getters.getConfigMidiDeveice()
         //console.log('[midi_devices]', midi_devices.input==null, midi_devices.output==null)
         if (this.$store.getters.getDemoMode()) {
           return true
         }
         if (midi_devices.input == null && midi_devices.output == null) {
           return true
         }
         return false
       }
     },
     storedPatchList: {
       get: function() {
         const patches = this.$store.getters.getAllStoredPatch()
         patches.forEach( (item, idx) => {
           if (typeof item.decoded.header != 'undefined'
               && typeof item.decoded.trailer_f7 != 'undefined') {
             patches[idx].decoded.fetched = true
           }
         })
         return patches
       }
     },
     workingPatch: {
       get: function() {
         return this.$store.getters.getWorkingPatchIdx()
       }
     },
   },
   watch: {
     'mutiple_fetch_menu.show'(newValue) {
       if (newValue) {
         this.$set(this.$data.mutiple_fetch_menu, 'value', [0, 99])
       }
     },
     'midi_device_alert_message.type'() {
       setTimeout( () => {
         this.$set(this.$data.midi_device_alert_message, 'show', false)
       }, this.midi_device_alert_message_show_duration)
     },
     patch_no(newValue) {
       const min = this.patch_no_conf.min
       const max = this.patch_no_conf.max
       if (min > newValue) {
         this.patch_no = min
       } else if (max < newValue) {
         this.patch_no = max
       }
       if (this.patch_no == 100) {
         this.patch_no_ewi = 0
       } else {
         this.patch_no_ewi = this.patch_no
       }
     },
     workingPatch() {
       //const working_idx = this.$store.getters.getWorkingPatchIdx()
       //const patch = this.$store.getters.getOneStoredPatch(working_idx)
       const workingPatch = this.$store.getters.getWorkingPatch()
       const patch = this.$store.getters.getOneStoredPatch(workingPatch.idx)
       this.patch_no = workingPatch.idx
       this.patch_name = patch.decoded.patchNameDecoded
     },
