     },
     sendArrayChannelMessage: async function(arr_message=[]) {
       if (arr_message.length > 0) {
         if (this.$store.getters.isUsingBleMIDI()) {
           const characteristic = this.$data.globalVar.libs.bleUtil.getCharacteristic()
           await characteristic.output.send(arr_message)
         } else {
           this.$data.globalVar.libs.ewi4ksd.sendArrayChannelMessage(arr_message)
         }
       }
     },
     handleDeviceCommunicationDialog: function(open = false) {
       this.$set(this.$data, 'midi_device_dialog', open)
       this.$set(this.$data.midi_device_alert_message, 'show', false)
       //this.midi_device_dialog = open
     },
     cancelMultiplePatches: function() {
       const btnText = this.component_overly_w_dialog.btnText.toLocaleLowerCase()
       const range = this.$data.mutiple_fetch_menu.value
       switch(btnText) {
         case 'close':
           this.$set(this.$data.component_overly_w_dialog, 'show', false)
           if (this.component_overly_w_dialog.timerId>0) {
             clearTimeout(this.component_overly_w_dialog.timerId)
             this.$set(this.$data.component_overly_w_dialog, 'timerId', 0)
           }
           break;
         case 'stop':
           this.$set(this.$data.component_overly_w_dialog, 'text', `Fetching canceled. Fetched: ${this.component_overly_w_dialog.progressValueNum} / ${range[1] - range[0]}`)
           this.$set(this.$data.component_overly_w_dialog, 'btnText', 'Close')
           this.$set(this.$data.component_overly_w_dialog, 'btnColor', 'primary')
           this.$set(this.$data.component_overly_w_dialog, 'progressStream', false)
           this.$set(this.$data, 'mutiple_fetch_isCanceled', true)
           this.component_overly_w_dialog.timerId = setTimeout( () => {
             this.$set(this.$data.component_overly_w_dialog, 'show', false)
             this.$set(this.$data.component_overly_w_dialog, 'text', '')
             this.$set(this.$data.component_overly_w_dialog, 'progressStream', true)
             this.$set(this.$data, 'mutiple_fetch_isCanceled', false)
           }, this.component_overly_w_dialog.closeDialogShowDuration)
           break;
       }
     },
     sleep: async (msec=0) => {
       new Promise(resolve => setTimeout(resolve, msec))
     },
     fetchMutiplePatches: async function() {
       this.$set(this.$data.mutiple_fetch_menu, 'show', false)
       const sleep = msec => new Promise(resolve => setTimeout(resolve, msec))
       const range = this.$data.mutiple_fetch_menu.value
       this.$set(this.$data.component_overly_w_dialog, 'btnColor', 'red')
       this.$set(this.$data.component_overly_w_dialog, 'btnText', 'Stop')
       this.$set(this.$data.component_overly_w_dialog, 'show', true)
       this.$set(this.$data.component_overly_w_dialog, 'btnDisabled', false)
       this.$set(this.$data.component_overly_w_dialog, 'progressStream', true)
       for(let i=range[0]; i<=range[1]; i++) {
         // handle when canceled
         if (this.mutiple_fetch_isCanceled) {
           this.$set(this.$data, 'mutiple_fetch_isCanceled', false)
           return
         }
         const progress = Math.floor(100 * ((i - range[0])/(range[1] - range[0])))
         this.$set(this.$data.component_overly_w_dialog, 'progressValue', progress)
         this.$set(this.$data.component_overly_w_dialog, 'text', `Fetched: ${i-range[0]} / ${(range[1] - range[0])}  (#${range[0]} - ${range[1]})`)
         this.$set(this.$data.component_overly_w_dialog, 'progressValueNum', i)
         await this.fetchOnePatchFromEwi4ks(i)
         await sleep(500)
         //const storedPatch = this.$store.getters.getOneStoredPatch(i)

         // scroll Patch List
         const scrool_to = i-1
         if (scrool_to > 0 && i<range[1]) {
           const elem = document.querySelector('div#patch-list').querySelector(`#patchno-${scrool_to}`)
           elem.scrollIntoView({
             behavior: 'smooth'
           })
         }
         await sleep(500)
       }
       this.$set(this.$data.component_overly_w_dialog, 'btnColor', 'primary')
       this.$set(this.$data.component_overly_w_dialog, 'btnText', 'close')
       this.$set(this.$data.component_overly_w_dialog, 'text', 'Fetching completed!!!')
       setTimeout(() => {
         this.$set(this.$data.component_overly_w_dialog, 'show', false)
         this.$set(this.$data.component_overly_w_dialog, 'btnColor', 'red')
         this.$set(this.$data.component_overly_w_dialog, 'progressValue', 0)
         this.$set(this.$data.component_overly_w_dialog, 'component_overly_w_dialog.text', '')
       }, this.component_overly_w_dialog.closeDialogShowDuration)

     },
     _blinkOnStateChange: function() {
       if (this.midi_on_state_change.timerId!=null) {
         clearTimeout(this.midi_on_state_change.timerId)
         this.midi_on_state_change.timerId=null
       }
       this.midi_on_state_change.color = this.midi_on_state_change.color_set.changed
       this.midi_on_state_change.timerId = setTimeout( () => {
         this.midi_on_state_change.color = this.midi_on_state_change.color_set.normal
       }, 800)
     },
     onStateChangeCallback: function(ret_midi_devices) {
       // vvv blink mdi-midi-port vvv
       this._blinkOnStateChange.bind(this)()
       // ^^^ blink mdi-midi-port ^^^
       this.setMidiDevicesToList.bind(this)({devices: ret_midi_devices})
       const midiConfig = this.$store.getters.getConfigMidiDeveice()
       if (midiConfig.input != null) {
         let input_found = false
         for (let i=0; i<ret_midi_devices.inputs.length; i++) {
           if (ret_midi_devices.inputs[i].id == midiConfig.input) {
             input_found = true
             break
           }
         }
         if (!input_found) {
           this.$store.commit('onUpdateSelectedMidiDevice', {type: 'input', idx: null})
           this.$set(this.$data, 'sel_midi_device_input', null)
           console.log('-------------- onUpdateSelectedMidiDevice : input : ', this.$store.getters.getConfigMidiDeveice())
         }
       }
       if (midiConfig.output != null) {
         let output_found = false
         for (let i=0; i<ret_midi_devices.outputs.length; i++) {
           if (ret_midi_devices.outputs[i].id == midiConfig.output) {
             output_found = true
             break
           }
         }
         if (!output_found) {
           this.$store.commit('onUpdateSelectedMidiDevice', {type: 'output', idx: null})
           this.$set(this.$data, 'sel_midi_device_output', null)
         }
       }
     },
     refreshListMidiDevices: function() {
       const ret_midi_devices = this.$data.globalVar.libs.wm.getMidiDevices()
       this.setMidiDevicesToList.bind(this)({devices: ret_midi_devices})
       //this.$set(this.$data, 'is_checking_midi_device', false)
     },
     listMidiDevices: async function() {
       if (this.$store.getters.getDemoMode() == false) {
         this.$set(this.$data, 'is_checking_midi_device', false)
         this.$set(this.$data, 'select_midi_device_disabled', false)
       }
       this.$data.globalVar.libs.wm.setOnStateChangeCallback(this.onStateChangeCallback.bind(this))
       this.$data.globalVar.libs.wm
           .listMIDIDevices(this.requestMIDIAccessOption).then( ret_midi_devices => {
             console.log(ret_midi_devices)
             this.setMidiDevicesToList.bind(this)(ret_midi_devices)
           })
     },
     setMidiDevicesToList: function(ret_midi_devices = {}) {
       if (Object.keys(ret_midi_devices).length == 0) return
       this. midi_devices = {
         inputs: [],
         outputs: []
       }
       ret_midi_devices.devices.inputs.forEach( item => {
         this.midi_devices.inputs.push({
           text: item.name,
           value: item.id
         })
       })
       ret_midi_devices.devices.outputs.forEach( item => {
         this.midi_devices.outputs.push({
           text: item.name,
           value: item.id
         })
       })
     },
     startBleMidiSearch: async function() {
       if (this.ble_button.text.toLocaleLowerCase() == 'connect') {
         this.$set(this.$data.ble_button, 'loading', true)
         const connectedBleCallback = async () => {
           console.log(this.$data.globalVar.libs.bleUtil)
           if (this.$data.globalVar.libs.bleUtil.getDeviceConnected()) {
             this.$set(this.$data.ble_button, 'icon', 'mdi-bluetooth-connect')
             this.$set(this.$data.ble_button, 'text', 'Disconnect')
             this.$set(this.$data.ble_button.color, 'btn', 'blue darken-2')
             this.$set(this.$data.ble_button.color, 'text', 'white--text')
           }
           /////////////////
           const characteristic = this.$data.globalVar.libs.bleUtil.getCharacteristic()
           this.$data.globalVar.libs.ewi4ksd.setMidiDevice('output', characteristic.output)
           this.$data.globalVar.libs.ewi4ksd.setMidiDevice('input', characteristic.input)
           this.$data.globalVar.libs.ewi4ksd.requestDeviceId((event) => {console.log(event)})
         }
         this.$data.globalVar.libs.bleUtil.setConnectedBleCallback(connectedBleCallback)

         const sleep = msec => new Promise(resolve => setTimeout(resolve, msec));
         const msgToExclude = this.$data.globalVar.libs.ewi4ksd.getMsgToExclude()
         const sysExCmd = this.$data.globalVar.libs.ewi4ksd.getSysExCommand()
         const self = this
         const midiEventHandleCallback = async (event) => {
           const midimsg = event.detail
           await sleep(100)
           if (!msgToExclude.includes(midimsg[0])
               && (midimsg.slice().shift() == sysExCmd.MIDI_SYSEX_HEADER
                        && midimsg.slice().pop() == sysExCmd.MIDI_SYSEX_TRAILER) ) {
             console.log(midimsg.slice().shift(), midimsg, midimsg.slice().pop())
             const isAkaiEwi4ks = this.$data.globalVar.libs.ewi4ksd.isAkaiEwi4ks(midimsg)
             let tmp_midi_device_alert_message = {}
             if (isAkaiEwi4ks) {
               this.$store.commit('onUpdateSelectedMidiDevice', {type: 'blemidi'})
               tmp_midi_device_alert_message = {
                 text: 'EWI4000s found!',
                 type: 'success',
                 show: true
               }
               this.$set(this.$data, 'midi_device_alert_message', tmp_midi_device_alert_message)
               this.$set(this.$data, 'is_checking_midi_device', false)
               this.$set(this.$data, 'select_midi_device_disabled', false)
             } else {
               tmp_midi_device_alert_message = {
                 text: 'EWI4000s does NOT found!',
                 type: 'error',
                 show: true
               }
               this.$set(this.$data, 'midi_device_alert_message', tmp_midi_device_alert_message)
               this.$set(this.$data, 'is_checking_midi_device', false)
               this.$set(this.$data, 'select_midi_device_disabled', false)
             }
             console.log(`isAkaiEwi4ks=[${isAkaiEwi4ks}] hide_label=[${this.timeoutToHide}]`)
             setTimeout( () => {
               self.midi_checkdevice_loader = false
               tmp_midi_device_alert_message = {
                 text: '',
                 type: null,
                 show: false
               }
               self.$set(self.$data, 'midi_device_alert_message', tmp_midi_device_alert_message)
               self.$data.globalVar.libs.bleUtil.setConnectedBleCallback(() => {})
               self.$data.globalVar.libs.bleUtil.setMidiEventHandleCallback(() => {})
             }, this.timeoutToHide)
           }
         }
         this.$data.globalVar.libs.bleUtil.setMidiEventHandleCallback(midiEventHandleCallback)
         //this.$data.globalVar.libs.bleUtil.setMIDIParser( (msg) => { return msg } )

         await this.$data.globalVar.libs.bleUtil.startBle()
         this.$set(this.$data.ble_button, 'loading', false)
       } else
       if (this.ble_button.text.toLocaleLowerCase() == 'disconnect') {
         this.$data.globalVar.libs.bleUtil.endBle()
         this.$set(this.$data.ble_button, 'icon', 'mdi-bluetooth')
         this.$set(this.$data.ble_button, 'text', 'Connect')
         this.$set(this.$data.ble_button.color, 'btn', '')
         this.$set(this.$data.ble_button.color, 'text', '')
       }
     },

