     fetchOnePatchFromEwi4ks: async function(_patchNo = null) {
       // [TODO] Display error message by connection status of EWI 4000s
       this.$store.commit('onUpdateStatusExpandPanel', {value: []})
       await this._setMidiDevice(true)
       let patchNo = Number(this.patch_no_ewi)
       if (_patchNo!=null) {
         patchNo = _patchNo
       }
       console.log(`[fetchOnePatchFromEwi4ks] patchNo=[${patchNo}]`)

       const self = this
       function onMidiMessage(event, patchNo, storedPatch) {
         //const patch = self.$data.globalVar.libs.ewi4ksd.getOneStoredPatch(patchNo)
         const patch = storedPatch
         console.log('[fetchOnePatchFromEwi4ks]', storedPatch)

         //self.$store.commit('onSetWorkingPatchIdx', {idx: 'idx', value: patchNo})
         self.$store.commit('onSetStoredPatchList', {idx: JSON.parse(JSON.stringify(patchNo)), value: patch})

         const editedPatch = self.$store.getters.getOneEditPatch(patchNo)
         if (!editedPatch.fetched) {
           self.$store.commit('onSetEditPatchList', {idx: patchNo, value: JSON.parse(JSON.stringify(patch)), mode: 'update'})
         } else {
           // [TODO]
           console.error('[ERROR] Patch already exists. Need to define what should handle this case.')
         }
       }

       if (this.$store.getters.isUsingBleMIDI()) {
         const msgFetchOnePatch = this.$data.globalVar.libs.ewi4ksd.getMessageFetchOnePatch(patchNo)
         console.log(msgFetchOnePatch)
         const characteristic = this.$data.globalVar.libs.bleUtil.getCharacteristic()
         const msgToExclude = this.$data.globalVar.libs.ewi4ksd.getMsgToExclude()
         const self = this
         const midiEventHandleCallback = (event) => {
           const midimsg = event.detail
           const str_midiMsg = []
           midimsg.forEach( val => {
             str_midiMsg.push(val.toString(16))
           })
           //console.log(str_midiMsg.join(' '))
           if (!msgToExclude.includes(midimsg[0])) {
             const decodedPatch = self.$data.globalVar.libs.ewi4ksd.decodeOnePatch(patchNo, midimsg)
             self.$data.globalVar.libs.ewi4ksd.setOneStoredPatch(patchNo, midimsg, decodedPatch)
             const storedPatch = self.$data.globalVar.libs.ewi4ksd.getOneStoredPatch(patchNo)
             const patch = storedPatch
             self.$store.commit('onSetStoredPatchList', {idx: JSON.parse(JSON.stringify(patchNo)), value: patch})
             const editedPatch = self.$store.getters.getOneEditPatch(patchNo)
             if (!editedPatch.fetched) {
               self.$store.commit('onSetEditPatchList', {idx: patchNo, value: JSON.parse(JSON.stringify(patch)), mode: 'update'})
             } else {
               // [TODO]
               console.error('[ERROR] Patch already exists. Need to define what should handle this case.')
             }
           }
         }
         this.$data.globalVar.libs.bleUtil.setMidiEventHandleCallback(midiEventHandleCallback)
         characteristic.output.send(msgFetchOnePatch)
       } else {
         await this.$data.globalVar.libs.ewi4ksd.fetchOnePatch(patchNo, onMidiMessage.bind(this.$data.globalVar.ewi4ksd))
       }
     },
     showOnePatchOnUi: async function(patchNo = null, closeDialog=true, closePanels=true) {
       if (patchNo==null) return
       if (closeDialog) this.$set(this.$data, 'midi_device_dialog', false)

       // cloase all panels
       if (closePanels) {
         console.log(`MidiDeviceControls.vue:showOnePatchOnUi() closePanels=[${closePanels}]`)
         //this.$store.commit('onUpdateStatusExpandPanel', {value: []})
       }

       const applyOnePatch = async (patchNo = 0) => {
         this.$store.commit('onSetWorkingPatchIdx', {idx: 'idx', value: patchNo})
         const storedPatch = this.$store.getters.getOneStoredPatch(patchNo)
         if (storedPatch.decoded.fetched===false) return
         //console.log(`getWorkingPatchIdx=[${this.$store.getters.getWorkingPatchIdx()}]`)
         //console.log(`applyOnePatch() patchNo=[${patchNo}]`, storedPatch)

         // initialize Link switch when load(reload) new patch
         const oscFilterLink = this.$store.getters.getWorkingPatchFilterLink('oscFilter')
         const noiseFilterLink = this.$store.getters.getWorkingPatchFilterLink('noiseFilter')
         this.$store.commit('onUpdateWorkingPatchFilterLink', {idx: 'oscFilter', value: oscFilterLink})
         this.$store.commit('onUpdateWorkingPatchFilterLink', {idx: 'noiseFilter', value: noiseFilterLink})

         // check syned status
         this.$store.commit('onCheckIsWorkingPatchSynced')

         // exit demo mode to enable all button & slider etc.
         this.$store.commit('onUpdateEnableControl')

         // update synced status parametet in store
         //this.$router.app.$children[0].updateParamIsSyncedApp.bind(this)()
         this.$router.app.$children[0].updateParamIsSyncedApp()

         //this.$router.app.$children[0].repaintAllPanelApp.bind(this)()

         // for the case when load same working patch after switching between modes.
         this.$router.app.$children[0].repaintAllPanelApp()

       }
       await applyOnePatch(patchNo)

       // send program change if device is selected (hopefully connected)
       const midiConfig = this.$store.getters.getConfigMidiDeveice()
       if (midiConfig.input != null && midiConfig.output != null) {
         const internalPatchNo = this.$data.globalVar.libs.ewi4ksd.convertPatchNumToInternal(this.$store.getters.getWorkingPatchIdx())
         console.log(internalPatchNo)
         const messages = []
         // Program Change
         //messages.push(new Uint8Array([0xc0, Number(internalPatchNo)]))
         this.$router.app.$children[0].sendArrayChannelMessageApp(messages)
       }
     },
     discardParamEditOnePatch: function() {
       const patchNo = this.$store.getters.getWorkingPatchIdx()
       this.$store.commit('onDiscardSelectedPatch', {idx: patchNo})
       this.$router.app.$children[0].showOnePatchOnUiApp(patchNo, true, false)
       this.$router.app.$children[0].repaintAllPanelApp()
     },
     closeSnackbarThenOpenDeviceCommunicationDialog() {
       this.$set(this.$data.snackbar, 'show', false)
       this.$set(this.$data, 'midi_device_dialog', true)
     },
     closePatchToDeviceDialog: function() {
       this.$set(this.$data.midiSyncToDeviceMenu, 'show', false)
     },
     openSyncParamEditOnePatchToDeviceDialog: async function() {
       this.$set(this.$data.midi_device_alert_message, 'show', false)

       // check input validity in form parameter
       if (!this.$store.getters.getIsValidationOK()) {
         this.$set(this.$data.snackbar, 'icon', this.snackbar.iconSet.error)
         this.$set(this.$data.snackbar, 'text', 'Fix Patch name.')
         this.$set(this.$data.snackbar, 'btnStyle', 'display: none;')
         this.$set(this.$data.snackbar, 'timeout', -1)
         this.$set(this.$data.snackbar, 'show', true)
         return
       }

       const midiDevice = this.$store.getters.getConfigMidiDeveice()

       // [TODO] check device list and update sync to device button status

       const demoMode = this.$store.getters.getDemoMode()
       if (demoMode) {
         // hide all and display message
         this.$set(this.$data.snackbar, 'icon', this.snackbar.iconSet.warning)
         this.$set(this.$data.snackbar, 'text', 'Exit "Demo Mode" to Sync → ')
         this.$set(this.$data.snackbar, 'btnStyle', 'display: unset;')
         this.$set(this.$data.snackbar, 'timeout', -1)
         this.$set(this.$data.snackbar, 'show', true)
         return
       }

       // initialize
       this.$set(this.$data.midiSyncToDeviceMenu, 'SyncBtnIsDisabled', true)
       this.$set(this.$data.midiSyncToDeviceMenu, 'show', true)
       if (midiDevice.input==null || midiDevice.output==null) {
         this.$set(this.$data.midiSyncToDeviceMenu, 'text', 'Select EWI4000s\'s MIDI IN Port & OUT Port.')
       }
       if (demoMode===false
           || (midiDevice.input!=null || midiDevice.output!=null)) {
         this.$set(this.$data.midiSyncToDeviceMenu, 'text', 'Select EWI4000s\'s MIDI IN Port & OUT Port.')
         this.$set(this.$data.midiSyncToDeviceMenu, 'SyncBtnIsDisabled', false)
       }

       return
     },
     async sendOnePatch(encodedPatch=null, onmidimessage=() => {}, mode='store') {
       if (encodedPatch==null) {
         return
       }
       if (this.$store.getters.isUsingBleMIDI()) {
         const characteristic = this.$data.globalVar.libs.bleUtil.getCharacteristic()
         //this.$data.globalVar.libs.bleUtil.setDebugMode(true)
         this.$data.globalVar.libs.bleUtil.setMidiEventHandleCallback(() => { })
         await characteristic.output.send(encodedPatch)
         if (mode=='edit') {
           console.log('[SEND as EDIT MODE]')
           const sysExCmd = this.$data.globalVar.libs.ewi4ksd.getSysExCommand()
           encodedPatch[4] = sysExCmd.MIDI_SYSEX_MODE_EWI_EDIT
           const tmp_internalPatchNo = encodedPatch[5]
           encodedPatch[5] = 0x00
           await characteristic.output.send(encodedPatch)
           encodedPatch[5] = tmp_internalPatchNo
         }
       } else {
         await this.$data.globalVar.libs.ewi4ksd.sendOnePatch(encodedPatch, onmidimessage, mode)
       }
     },
     encodeOnePatch(editPatch_decoded) {
       return this.$data.globalVar.libs.ewi4ksd.encodeOnePatch(editPatch_decoded)
     },
     syncParamEditOnePatchToDevice: async function() {
       const midiDevice = this.$store.getters.getConfigMidiDeveice()
       if (midiDevice.input==null || midiDevice.output==null) {
         this.openSyncParamEditOnePatchToDeviceDialog()
         return
       }

       // switch button to loading
       this.$set(this.$data.midiSyncToDeviceMenu, 'loading', true)
       this.$set(this.$data, 'select_midi_device_disabled', true)
       this.$set(this.$data.midiSyncToDeviceMenu, 'closeBtnDisabled', true)

       // vvv check script vvv
       this.revealSysExDiff()
       // ^^^ check script ^^^

       // send message to store/update
       const patchNo = this.$store.getters.getWorkingPatchIdx()
       const storedPatch = this.$store.getters.getOneStoredPatch(patchNo)
       const editPatch = this.$store.getters.getOneEditPatch(patchNo)
       //const encodedPatch = this.$data.globalVar.libs.ewi4ksd.encodeOnePatch(editPatch.decoded)
       const encodedPatch = this.encodeOnePatch(editPatch.decoded)
       const arr_storedPatch = Object.values(storedPatch.hex)
       console.log('[_onSyncParamEditOnePatchToDevice:stored]', arr_storedPatch)
       console.log('[_onSyncParamEditOnePatchToDevice:edit]', encodedPatch)

       try {
         await this.sendOnePatch(encodedPatch)
         //await this.$data.globalVar.libs.ewi4ksd.sendOnePatch(encodedPatch)

         this.$store.commit('onSetStoredPatchList', {idx: patchNo, value: JSON.parse(JSON.stringify(editPatch))} )
         this.$router.app.$children[0].updateParamIsSyncedApp.bind(this)()

         // switch button to loading
         this.$set(this.$data.midiSyncToDeviceMenu, 'loading', false)
         this.$set(this.$data, 'select_midi_device_disabled', false)
         this.$set(this.$data.midiSyncToDeviceMenu, 'closeBtnDisabled', false)
         this.$set(this.$data.midiSyncToDeviceMenu, 'SyncBtnIsDisabled', true)
         this.$set(this.$data.midiSyncToDeviceMenu, 'show', false)

         // show snackbar
         this.$set(this.$data.snackbar, 'icon', this.snackbar.iconSet.success)
         this.$set(this.$data.snackbar, 'text', 'Successfully Synced!!')
         this.$set(this.$data.snackbar, 'btnStyle', 'display: none;')
         this.$set(this.$data.snackbar, 'timeout', this.timeoutToHide)
         this.$set(this.$data.snackbar, 'show', true)
       } catch (err) {
         console.log(err)
         // switch button to loading
         this.$set(this.$data.midiSyncToDeviceMenu, 'loading', false)
         this.$set(this.$data, 'select_midi_device_disabled', false)
         this.$set(this.$data.midiSyncToDeviceMenu, 'closeBtnDisabled', false)
         this.$set(this.$data.midiSyncToDeviceMenu, 'SyncBtnIsDisabled', true)
         this.$set(this.$data.midiSyncToDeviceMenu, 'show', true)

         // show snackbar
         this.$set(this.$data.snackbar, 'icon', this.snackbar.iconSet.error)
         this.$set(this.$data.snackbar, 'text', 'Something went wrong. Select ports and try again.')
         this.$set(this.$data.snackbar, 'btnStyle', 'display: none;')
         this.$set(this.$data.snackbar, 'timeout', this.timeout)
         this.$set(this.$data.snackbar, 'show', true)
       }
     },
     revealSysExDiff(patchNo=null) {
       if (patchNo==null) {
         patchNo = this.$store.getters.getWorkingPatchIdx()
       }
       const editPatch = this.$store.getters.getOneEditPatch(patchNo)
       const encodedPatch = this.$data.globalVar.libs.ewi4ksd.encodeOnePatch(editPatch.decoded)

       const storedPatch = this.$store.getters.getOneStoredPatch(patchNo)
       const arr_storedPatch = Object.values(storedPatch.hex)
       const sysexStructure = this.$data.globalVar.libs.ewi4ksd.getSysExStructure()
       const red     = '\u001b[31m'
       const green   = '\u001b[32m'
       for(let i=0; i<arr_storedPatch.length; i++) {
         const idx = i
         const isEdit = storedPatch.hex[idx]==encodedPatch[idx]
         let color = green
         if (!isEdit) {
           color=red
         }
         console.log(`${color}idx=[${idx}] paramName=[${sysexStructure.getNameByByteIdx(idx)}] isEdit=[${isEdit}] stored=[${('00'+storedPatch.hex[idx].toString(16)).substr(-2)}] encoded=[${('00'+encodedPatch[idx].toString(16)).substr(-2)}]`)
       }
     },
     exportPatches: function() {
       const allStoredPatch= this.$store.getters.getAllStoredPatch()
       let export_content = []
       allStoredPatch.forEach( patch => {
         export_content.push(JSON.stringify({
           decoded: patch.decoded,
           hex: patch.hex
         }))
       })
       this._downloadTextAsFile(export_content.join('\n--------\n'))
     },
     _downloadTextAsFile: function(content=null) {
       if (content==null) return
       const blob = new Blob([content], {type: 'text/plain'})
       const url = URL.createObjectURL(blob)
       const a = document.createElement('a')
       document.body.appendChild(a)
       a.download = `export_patch_${Date.now()}.txt`
       a.href = url
       a.click()
       a.remove()
       URL.revokeObjectURL(url)
     },
     _setMidiDevice: function(setStoreDevice=true) {
       const this_ewi4ksd = this.$data.globalVar.libs.ewi4ksd
       return new Promise( resolve => {
         this.$data.globalVar.libs.wm
             .listMIDIDevices(this.requestMIDIAccessOption)
             .then( ret_midi_access => {
               let midi_device = {}
               switch (setStoreDevice) {
                 case true:
                   midi_device = this.$store.getters.getConfigMidiDeveice()
                   break
                 case false:
                   midi_device = {
                     input: this.sel_midi_device_input,
                     output: this.sel_midi_device_output
                   }
                   break
               }
               const sel_input_id = midi_device.input
               const sel_output_id = midi_device.output

               if (sel_input_id===null || sel_output_id===null) {
                 this.midi_device_alert_message='Select Ports'
                 return
               }

               // set_midi_input
               for (let i=0; i<ret_midi_access.devices.inputs.length; i++) {
                 if (ret_midi_access.devices.inputs[i].id == sel_input_id) {
                   this.$data.globalVar.libs.ewi4ksd.setMidiDevice.bind(this_ewi4ksd)('input', ret_midi_access.devices.inputs[i])
                   break
                 }
               }
               // set_midi_output
               for (let i=0; i<ret_midi_access.devices.outputs.length; i++) {
                 if (ret_midi_access.devices.outputs[i].id == sel_output_id) {
                   this.$data.globalVar.libs.ewi4ksd.setMidiDevice.bind(this_ewi4ksd)('output', ret_midi_access.devices.outputs[i])
                   break
                 }
               }
               resolve()
             })
       })
     },
     _requestMidiDeviceId: function(_callback = null) {
       let callback = _callback
       if (callback==null) {
         callback = function(ret) {
           console.log(ret.hex_message)
           if (ret.is_akai_ewi4ks) {
             const tmp_midi_device_alert_message = {
               text: 'AKAI EWI4000s found!',
               type: 'success',
               show: true
             }
             console.log(tmp_midi_device_alert_message)
             this.$set(this.$data, 'midi_device_alert_message', tmp_midi_device_alert_message)
           }
         }
       }
       this._setMidiDevice(false).then ( async () => {
         this.midi_checkdevice_loader = true
         // [WARNING] bug callback() will NOT fired if set callbak by callback.bind(this)
         await this.$data.globalVar.libs.ewi4ksd.requestDeviceId(callback)
         setTimeout( () => {
           this.midi_checkdevice_loader = false
         }, this.timeoutToHide)
       })
     },
   }
 }
</script>

