// Uint8Arrayに変換
sample_patches.forEach( (patch) => {
  Object.keys(patch.decoded).forEach( (key) => {
    if (typeof patch.decoded[key][0]=='number') {
      const elem = []
      Object.keys(patch.decoded[key]).forEach((idx) => {
        elem.push(patch.decoded[key][idx])
      })
      patch.decoded[key] = Uint8Array.from(elem)
      //if (key.match(/patchName/)!=null) console.log(key, patch.decoded[key], Uint8Array.from(elem))
    }
  })
})

export { sample_patches }
