import Vue from 'vue'
import Router from 'vue-router'

import EWI4000Editor from '../components/EWI4000Editor.vue'
import HelloWorld from '../components/HelloWorld.vue'
import Experimental from '../components/Experimental.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/apps/ewi4000seditor',
  routes: [
    {
      path: '/',
      name: 'EWI4000Editor',
      component: EWI4000Editor,
    },
    {
      path: '/h',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/e',
      name: 'Experimental',
      component: Experimental
    }


  ]
})

